      <header>
        <div class="header-img-box">
          <div class="header-left">
            <div  class="header-left-cont">
                 <div class="head-left">
          <img src="../assets/images/home/logo.png" class="head-img" />
          <img src="../assets/images/home/logoborder.png" style="position:relative;top:-50px;left:-5px;width:100px" class="head-img" />
        </div>
        
              
            </div>
          </div>
          <div class="header-c" @click="backBtn"><img width="30px" src="../assets/img/back.png" alt=""><div>返回</div></div>
        </div>
        <div class="header-img-h">
          <img src="../assets/images/home/headbg.png" class="notice-img" />
        </div>
      </header>
      <div class="fusionEnd-conter">
        <div class="fusionEnd-conter-box">
          <cardModel :vname="vname" :CardInfo="CardInfo" />
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import cardModel from "../model/cardModel";
export default {
  data() {
    return {
      vname: "giveUpInfo",
      CardInfo: {},
    };
  },
  components: {
    cardModel,
  },
  methods:{
        backBtn(){
      this.$router.push("/backpack");
    },
  },
  mounted() {
    var CardInfo = JSON.parse(sessionStorage.getItem("objList"));

    CardInfo.imgUrl = require("../assets/cardimg/" + CardInfo.imgUrlC + ".png");
    if (CardInfo) {
      CardInfo.imgUrl = require("../assets/cardimg/" +
        CardInfo.imgUrlC +
        ".png");

      if (CardInfo.genre == "5") {
        CardInfo.cardType = "白卡";
      }
      if (CardInfo.genre == "4") {
        CardInfo.cardType = "蓝卡";
      }
      if (CardInfo.genre == "3") {
        CardInfo.cardType = "紫卡";
      }
      if (CardInfo.genre == "2") {
        CardInfo.cardType = "橙卡";
      }
      if (CardInfo.genre == "1") {
        CardInfo.cardType = "金卡";
      }
      this.CardInfo = CardInfo;
    }
  },
};
</script>
